import { API_URL } from '@/assets/config'
import { NoticeType } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Service from '@/assets/service/service'
export default {
  name: 'Home',
  data () {
    return {
      api_url: API_URL,
      hasImg: false,
      noticeList: [],
      noticeList2: [],
      noticeType: NoticeType,
      noticeTotal: 0,
      showTable: false,
      statisticsData: {},
      statisticsOptions: [
        {
          title: '学术队伍',
          key: 'college_people',
          options: [
            { title: '学部人员', key: 'num_of_college_people' },
            { title: '博士', key: 'num_of_doctor' },
            { title: '硕士', key: 'num_of_master' },
            { title: '人才计划', key: 'num_of_talent_plan' },
            { title: '学术委员会', key: 'num_of_academic' },
            { title: '伦理委员会', key: 'num_of_ethics' }
          ]
        },
        {
          title: '科学研究',
          key: 'researcher',
          options: [
            { title: '纵向项目', key: 'portrait' },
            { title: '横向项目', key: 'landscape' },
            { title: '英文论文', key: 'english_paper' },
            { title: '中文论文', key: 'chinese_paper' },
            { title: '学术著作', key: 'academic_works' },
            { title: '会议论文', key: 'meeting_paper' },
            { title: '研究报告', key: 'research_report' },
            { title: '科研/教学获奖', key: 'research_awards' }
          ]
        },
        {
          title: '学术交流',
          key: 'academic_exchange',
          options: [
            { title: '主办会议', key: 'num_of_host_meeting' },
            { title: '参加会议', key: 'num_of_participate_meeting' },
            { title: '主办培训', key: 'num_of_host_training' },
            { title: '参加培训', key: 'num_of_participate_training' }
          ]
        },
        //{
        //  title: '人才培养',
        //  key: 'talent_development',
        //  options: [
        //    { title: '硕士培养-毕业', key: 'num_of_master_train_graduation' },
        //    { title: '硕士培养-在读', key: 'num_of_master_train_school' },
        //    { title: '博士培养-毕业', key: 'num_of_doctor_train_graduation' },
        //    { title: '博士培养-在读', key: 'num_of_doctor_train_school' },
        //    { title: '教学获奖', key: 'num_of_teacher_award' },
        //    { title: '精品课程', key: 'num_of_good_course' },
        //    { title: '教材建设', key: 'num_of_courseTeaching_material' }
        //  ]
        //},
        {
          title: '管理工作',
          key: 'management_work',
          options: [
            { title: '规章制度', key: 'rules_regulations' },
            { title: '年度简报', key: 'annual_briefing' }
          ]
        }
      ]
    }
  },
  methods: {
    getNotice () {
      Service.Notice.get().then(response => {
        if (response.code == 200) {
          this.noticeList = response.data.data
          this.noticeTotal = response.data.total
          this.noticeList2 = this.noticeList.filter(notice => {
            if (notice.pic) {
              this.hasImg = true
              return notice
            }
          })
        }
      })
    },
    getStatistics () {
      Service.Statistics.get().then(response => {
        if (response.code == 200) {
          this.statisticsData = response.data
          this.showTable = true
        }
      })
    },
    more () {
      tabsService.addTab('/views/notice', '/views/notice', '公告管理')
    },
    details (item) {
      let notice
      switch (Number(item.type)) {
        case 1:
          notice = this.noticeType.school
          break
        case 2:
          notice = this.noticeType.department
          break
        case 3:
          notice = this.noticeType.writing
          break
      }
      tabsService.addDetailsTab('/views/notice/details/' + item.id + '?type=' + notice.type + '&readonly=readonly', notice.name + '-详情-' + item.id)
    }
  },
  created () {
    this.getNotice()
    this.getStatistics()
  }
}



